.coin{
    padding: 3em 2em 3em 2em;
    display: flex;
    .coinImg{
        width: 6em;
        height: 6em;
        align-self: center;
    }
    .coin-desc{
        display: flex;
        flex-direction: column;
        margin-left: 4em;
    }
    .coin-name{
        .name{
            font-size: 1.5em;
            font-weight: bold;
            margin: 0;
            color: white;
        }
        .subname{
            margin-top: 0.5em;
            color: grey;
            text-transform: uppercase;
        }
    }
    .coin-info{
        display: flex;
        justify-content: space-between;
        font-size: 1em;
        .price{
            margin-right: 2em;
            color: white;
        }
    }
}